import React, { useState } from "react";
import SVG from "react-inlinesvg";
import validateIIN from "@/utils/validateIIN";
import OtpModal from "@/container/otpModal";
import { sendOtp } from "@/services/requests";
import { Layout } from "@/views/layout";
import { Modal, InputIin, Button } from "@/components";
import Instruction1 from "public/instruction1.svg";
import Instruction2 from "public/instruction2.svg";
import Instruction3 from "public/instruction3.svg";
import Instruction4 from "public/instruction4.svg";

import "./style.scss";

const HomePage = (props) => {
  const [iin, setIIN] = React.useState("");
  const [isOpen, setIsOpen] = React.useState(false);
  const [correctIin, setCorrectIin] = React.useState(false);

  const callBack = (res) => {
    setCorrectIin(res);
  };
  const mobile = window.innerWidth < 600;
  return (
    <Layout>
      <div className="home">
        <Modal isOpen={isOpen} handleFunc={() => setIsOpen(false)}>
          <OtpModal />
        </Modal>
        {mobile && (
          <section
            style={{
              paddingLeft: "20px",
              paddingRight: "20px",
              marginTop: "40px",
            }}
          >
            <InputIin
              border
              value={iin}
              mask="99 99 99 99 99 99"
              callBack={callBack}
              onChange={(e) => setIIN(e.target.value)}
              className={validateIIN(iin) ? "error" : ""}
              placeholder={"Введите ИИН"}
            />
            <div style={{ display: "flex", justifyContent: "center" }}>
              <Button
                style={{ width: "100%", marginBottom: "20px" }}
                className={"success"}
                onClick={() => setIsOpen(true)}
                label="Далее"
                disabled={!correctIin}
              />
            </div>
          </section>
        )}

        <div className="container">
          <section className="home__instruction">
            <section style={{ width: "286px" }}>
              {" "}
              <SVG src={Instruction1} />
            </section>
            <section style={{ width: "286px" }}>
              {" "}
              <SVG src={Instruction2} />
            </section>
            <section style={{ width: "286px" }}>
              {" "}
              <SVG src={Instruction3} />
            </section>
            <section style={{ width: "286px" }}>
              {" "}
              <SVG src={Instruction4} />
            </section>
          </section>
          {!mobile && (
            <section className="card">
              <div className="card_box">
                <div className="card_box-small">
                  <InputIin
                    border
                    value={iin}
                    mask="99 99 99 99 99 99"
                    callBack={callBack}
                    onChange={(e) => setIIN(e.target.value)}
                    className={validateIIN(iin) ? "error" : ""}
                    placeholder={"Введите ИИН"}
                  />
                  <div style={{ display: "flex", justifyContent: "center",marginTop:"15px"}}>
                    <Button
                      style={{ width: "100%", maxWidth:"250px" }}
                      className={"success"}
                      onClick={() => setIsOpen(true)}
                      label="Далее"
                      disabled={!correctIin}
                    />
                  </div>
                </div>
              </div>
            </section>
          )}
        </div>
      </div>
    </Layout>
  );
};

export default HomePage;
